<template>
	<div class="personal-page">
		<van-cell-group>
			<van-field label="头像" center input-align="right">
				<div class="edit-span" slot="input">
					<van-image :src="getAvatar">
						<template v-slot:loading>
							<van-loading type="spinner" size="20" />
						</template>
						<template v-slot:error>加载失败</template>
					</van-image>
					<!--<van-icon name="arrow" />-->
				</div>
			</van-field>
		</van-cell-group>
		<van-cell-group>
			<van-field label="姓名" v-model="info.realName" input-align="right" center readonly />
			<van-field label="单位" v-model="info.unit" @input="onInput" input-align="right" center placeholder="请输入单位" />
			<van-field label="部门" v-model="info.department" @input="onInput" input-align="right" center placeholder="请输入部门"/>
			<van-field label="职务" v-model="info.job" @input="onInput" input-align="right" center placeholder="请输入职务" />
			<van-field label="身份证号码" v-model="info.idCard" @input="onInput" input-align="right" center placeholder="请输入身份证号码" />
			<van-field label="性别" input-align="right" center @click="show1=true">
				<div class="edit-span" slot="input">
					<span>{{getSex}}</span>
					<van-icon name="arrow" />
				</div>
			</van-field>
			<van-field label="手机号码" v-model="info.phone" input-align="right" center readonly />
			<!--<van-field label="手机号码" input-align="right" center @click="show2=true">
				<div class="edit-span" slot="input">
					<span>{{info.phone}}</span>
					<van-icon name="arrow" />
				</div>
			</van-field>-->
			<van-field label="电子邮箱" v-model="info.email" @input="onInput" input-align="right" center placeholder="请输入电子邮箱" />
		</van-cell-group>

		<!--性别选项-->
		<van-dialog v-model="show1" showCancelButton :showConfirmButton="false">
			<van-radio-group v-model="sex" @change="onChange">
				<van-cell-group>
					<template v-for="(item, i) in sexs">
						<van-cell :title="item.name" clickable @click="onSelect(item)" :key="i">
							<van-radio slot="right-icon" :name="item.value"></van-radio>
						</van-cell>
					</template>
				</van-cell-group>
			</van-radio-group>
		</van-dialog>

		<!--设置手机号码-->
		<van-dialog v-model="show2" title="设置手机号" :showConfirmButton="false">
			<van-cell-group>
				<van-field v-model="phone" type="tel" maxlength="11" center clearable name="phone" placeholder="请输入手机号码" />
				<van-field v-model="verifycode" maxlength="4" center clearable placeholder="验证码">
					<van-button slot="button" size="small" type="primary" @click="sendVerifycode" :disabled="!disabledCodeBtn">{{codeText}}</van-button>
				</van-field>
			</van-cell-group>
			<div class="van-hairline--top van-dialog__footer van-dialog__footer--buttons">
				<button class="van-button van-button--default van-button--large van-dialog__cancel" @click="show2=false">
					<span class="van-button__text">取消</span>
				</button>
				<button class="van-button van-button--default van-button--large van-dialog__confirm van-hairline--left" @click="onConfirm">
					<span class="van-button__text">确认</span>
				</button>
			</div>
		</van-dialog>

		<div class="page-footer" v-if="isedit">
			<van-button round block type="primary" @click="submit">保存</van-button>
		</div>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	const avatar1 = require('../../assets/images/mail.png')
	const avatar2 = require('../../assets/images/femail.png')
	export default {
		name: 'personal',
		data() {
			return {
				show1: false,
				show2: false,
				sex: '',
				sexs: [{
					name: '男',
					value: 0
				}, {
					name: '女',
					value: 1
				}],
				phone: '',
				verifycode: '',
				codeText: '获取验证码',
				disabledCodeBtn: true,
				isedit: false
			}
		},
		computed: {
			...mapState(['flag', 'info']),
			getAvatar() {
				return this.info.sex ? avatar2 : avatar1
			},
			getSex() {
				return this.info.sex ? '女' : '男'
			}
		},
		mounted() {
			this.sex = this.info.sex
		},
		methods: {
			// 输入姓名
			onInput() {
				this.isedit = true
			},
			// 选择性别
			onSelect(item) {
				if(item.value !== this.sex) {
					this.isedit = true
					this.show1 = false
					this.info.sex = item.value
					this.sex = this.info.sex
				}
			},
			onChange() {
				this.isedit = true
				this.show1 = false
				this.info.sex = this.sex
			},
			// 向后台要验证码方法
			async sendVerifycode() {
				if(this.verifyPhone()) {
					this.$toast(this.verifyPhone())
				} else {
					this.countDown(60)
					let params = {
						phone: this.phone
					}
					let res = await this.$Request.get('/student/getVerificationCode.do', params)
					if(res && res.code === 0) {
						this.$toast.success('发送成功')
					}
				}
			},
			// 手机号码校验方法
			verifyPhone() {
				if(!this.phone) {
					return '请输入手机号'
				} else if(this.phone.length !== 11) {
					return '请输入11位手机号'
				} else {
					return false
				}
			},
			// 验证码校验方法
			verifyCode() {
				if(!this.verifycode) {
					return '请输入验证码'
				} else {
					return false
				}
			},
			// 倒计时方法
			countDown(time) {
				if(time === 0) {
					this.disabledCodeBtn = true
					this.codeText = "获取验证码"
					return
				} else {
					this.disabledCodeBtn = false;
					this.codeText = '重新发送(' + time + ')'
					time--
				}
				setTimeout(() => {
					this.countDown(time)
				}, 1000)
			},
			// 确定
			async onConfirm() {
				if(this.verifyPhone() || this.verifyCode()) {
					this.$toast(this.verifyPhone() || this.verifyCode())
				} else {
					let params = {
						phone: this.phone,
						verificationCode: this.verifycode
					}
					let res = await this.$Request.get('/student/queryStudentsByWeChat.do', params)
					if(res && res.code === 0) {
						this.show2 = false
						this.isedit = true
						this.info.phone = this.phone
						this.$toast.success('验证成功！')
					}
				}
			},
			// 表单校验方法
			verify() {
				if(!this.info.realName) {
					return '请输入姓名'
				} else {
					return false
				}
			},
			// 提交
			async submit() {
				if(this.verify()) {
					this.$toast(this.verify())
				} else {
					let params = Object.assign({ type: 1 }, this.info)
					let res = await this.$Request.post('/student/addOrEdit.do', params)
					if(res && res.rCode === 0) {
						this.isedit = false
						this.$toast.success('修改成功！')
						this.$Utils.setItem("flag", this.flag, 1)
						this.$Utils.setItem("info", this.info, 1)
						this.setFlag(this.flag)
						this.setInfo(this.info)
					}
				}
			},
			...mapMutations(['setFlag', 'setInfo'])
		}
	}
</script>

<style>

</style>


